import React, { useEffect, useState } from "react"
import {Link,graphql } from "gatsby"
import PropTypes from "prop-types"
import BreadcumHeader from "../components/breadcumHeader"
import Footer from "../components/footer"
import Clients from "../components/clients"
import Nav from "../components/nav"
import Axios from "../../axios"
import { BASE_URL } from "../../config"

export const query = graphql`
  query($slug: String!) {
    allStrapiServiceCategories(filter: {slug: {eq: $slug}}) {
        nodes {
            name
            slug
            service {
              services {
                services
              }
              name
              id
              img {
                localFile {
                  childImageSharp {
                    original {
                      src
                    }
                  }
                }
              }
            }
          }
      }
}
`

function ServiceDetailPage({ data }) {

    const {name, service} = data.allStrapiServiceCategories.nodes[0]
    return (
        <div>
        <Nav />
        <BreadcumHeader title={"Services"} heroImageName={"service"} />

        <section id="services">
            <section className="container">
            <section className="row mt-5">
                <section className="col-md-12 text-center">
                <h2 className="font-weight-bold">{name}</h2>
                <div className="hr m-3 mb-4">
                    <hr className="hr-msar-dark hr-map" style={{width:`${name?.length * 16}px`}} /> 
                </div>
                </section>
            </section>

            {service.map((document, i) => (
                <>
                {i % 2 === 0 ? (
                    <section className="row services-1 m-4 service-class">
                    <section className="col-md-5 order-2 order-md-1">
                        <img
                        className="service-img img-fluid"
                        src={document.img.localFile.childImageSharp?.original.src}
                        />
                    </section>
                    <section className="col-md-7 order-1 order-md-2">
                        <h5 className="my-lg-3 font-weight-bold">
                        {document?.name}
                        </h5>
                        <ul className="msar-ul small lh15">
                        {document.services.services.map(item => (
                            <li className>{item}</li>
                        ))}
                        </ul>
                    </section>
                    </section>
                ) : (
                    <section className="row services-2 m-4 service-class">
                    <section className="col-md-7">
                        <h5 className="my-3 font-weight-bold">
                        {document?.name}
                        </h5>
                        <ul className="msar-ul small lh15">
                        {document.services.services.map(item => (
                            <li className>{item}</li>
                        ))}
                        </ul>
                    </section>
                    <section className="col-md-5">
                        <img
                        className="service-img img-fluid"
                        src={document.img.localFile.childImageSharp?.original.src}
                        />
                    </section>
                    </section>
                )}
                </>
            ))}
            </section>
        </section>

        <Clients />
        <Footer />
    </div>
    )
}

export default ServiceDetailPage
